<template>
    <div id="Bienvenida">
        <button id="btnIngreso" v-on:click="procesarBienvenida" v-if="mostrarBotonIngresar">Ingresar</button>
        <form action="" v-if="!mostrarBotonIngresar && !verMensajeBienvenida">
            <div>
                <label for="nombre">Nombre</label>
                <input type="text" name="nombre" v-model="nombre">
            </div>
            <div>
                <label for="apellido">Apellido</label>
                <input type="text" name="apellido" v-model="apellido">
            </div>
            <div>
                <input id="btnForm" type="submit" value="Enviar" v-on:click.prevent="mostrarMensajeBienvenida">
            </div>
        </form>
        <div v-if="verMensajeBienvenida" id="mensajeBienvenida">
            <h2>Bienvenid@ {{ getFullName }}</h2>
            <p>Aquí podrás encontrar información de películas de anime de Studio Ghibli.</p>
            <button id="btnLimpiar" @click="limpiarInfo">Limpiar información</button>
        </div>
    </div>
</template>

<script>
export default {
    name: 'Bienvenida',
    data: function() {
        return {
            mostrarBotonIngresar: true,
            nombre: '',
            apellido: '',
            verMensajeBienvenida: false
        }
    },
    methods: {
        procesarBienvenida: function() {
            this.mostrarBotonIngresar = false;
            this.$emit('iniciar')
        },
        mostrarMensajeBienvenida: function() {
            this.verMensajeBienvenida = true;
            this.$emit('seccionPeliculas')
        },
        limpiarInfo: function() {
            this.nombre = '';
            this.apellido = '';
            this.verMensajeBienvenida = false;
            this.mostrarBotonIngresar = true;
            this.$emit('ocultarSeccionPeliculas')
        }
    },
    computed: {
        getFullName() {
            return this.nombre + ' ' + this.apellido
        }
    }
}
</script>

<style scoped>
#Bienvenida {
    color: black;
    display: flex;
    justify-content: center;
}
#btnIngreso {
    background-color: cornflowerblue;
    border: 0px;
    border-radius: 5px;
    padding: 1%;
    font-size: 20px;
    cursor: pointer;
}
#btnIngreso:active,
#btnForm:active,
#btnLimpiar:active {
    transform: translateY(4px);
}
form {
    display: flex;
    flex-direction: column;
    width: 30%;
    align-items: center;
    border: 2px orchid outset;
    border-radius: 5px;
    padding: 2%;
}
form div {
    margin-top: 5px;
    margin-bottom: 5px;
}
form label {
    margin-right: 5px;
    color: darkslateblue;;
}
form input {
    border: solid 1px blue;
    border-radius: 4px;
    outline: none;
    font-size: 15px;
}
input[type=text]:focus {
    border: 3px solid rgb(158, 158, 230);
}
#btnForm {
    background-color: darkslateblue;
    border: 0px;
    border-radius: 5px;
    padding: 8px;
    font-size: 15px;
    color: aliceblue;
    cursor: pointer;
}
#mensajeBienvenida {
    border: 2px solid greenyellow;
    border-radius: 5px;
    box-shadow: 2px 2px dimgrey;
    background-color: lightcyan;
    padding: 2%;
}
#btnLimpiar {
    background-color: cornflowerblue;
    border: 0px;
    border-radius: 5px;
    padding: 8px;
    font-size: 15px;
    color: aliceblue;
    cursor: pointer;
}
/* Dispositivos extra pequeños (teléfonos, 600px y menos) */
@media only screen and (max-width: 600px) {
    #btnIngreso {
    padding: 3%;
    }
    form {
    width: 80%;
    padding: 4%;
    }
}
@media only screen and (min-width: 600px) and (max-width: 767px) {
        #btnIngreso {
    padding: 3%;
    }
    form {
    width: 60%;
    padding: 2%;
    }
}
</style>