<template>
    <div id="Peliculas">
        <form action="">
            <label for="peliculas">Elija una película</label>
            <select name="peliculas" id="peliculas" v-model="index" @change="verPeliculaSeleccionada">
                <option value="" selected>...</option>
                <option :value="index" v-for="(pelicula, index) in datosPeliculas" :key="index">{{ pelicula.title }}</option>
            </select>
        </form>
        <div class="cadaPelicula" v-if="verInfoPelicula">
            <div class="divImg">
                <img :src="rutaImagen" :alt="nombreOriginal">
            </div>
            <div class="divInfo">
                <p><b>Título Original: </b>{{ nombreOriginal }}</p>
                <p><b>Título en inglés: </b>{{ nombreIngles }}</p>
                <p class="descrip"><b>Descripción: </b>{{ descripcionPelicula }}</p>
                <p><b>Director: </b>{{ directorPelicula }}</p>
                <p><b>Duración: </b>{{ duracionPelicula }}</p>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'Peliculas',
    props: {
        datosPeliculas: {
            type: Array,
            required: true
        }
    },
    data: function() {
        return {
            index: '',
            verInfoPelicula: false,
            nombreOriginal: '',
            nombreIngles:'',
            descripcionPelicula:'',
            directorPelicula:'',
            duracionPelicula:'',
            rutaImagen:'',
        }
    },
    methods: {
        verPeliculaSeleccionada: function() {
            this.verInfoPelicula = true;
            this.nombreOriginal = this.datosPeliculas[this.index].original_title_romanised;
            this.nombreIngles = this.datosPeliculas[this.index].title;
            this.descripcionPelicula = this.datosPeliculas[this.index].description;
            this.directorPelicula = this.datosPeliculas[this.index].director;
            this.duracionPelicula = this.datosPeliculas[this.index].running_time;
            this.rutaImagen = this.datosPeliculas[this.index].image;
        }
    }
}
</script>

<style scoped>
#Peliculas {
    display: flex;
    flex-direction: column;
    align-items: center;
}
form {
    margin-top: 30px;
    width: 30%;
    display: flex;
    flex-direction: column;
    align-items: center;
}
form label {
    color: darkslateblue;
    font-size: 22px;
    margin-bottom: 10px;
}
select {
    border: 2px solid cornflowerblue;
    border-radius: 5px;
    height: 30px;
    width: 90%;
    margin-bottom: 40px;
}
select option {
    font-size: 18px;
}
select option:hover {
    background-color: blueviolet;
}
::-webkit-scrollbar {
    width: 15px;
}
::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px grey; 
    border-radius: 10px;
}
::-webkit-scrollbar-thumb {
    background: rgb(13, 46, 105);
    border-radius: 10px;
}
::-webkit-scrollbar-thumb:hover {
    background: cornflowerblue; 
}
.cadaPelicula {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    width: 50%;
}
img {
    width: 360px;
    height: 540px;
}
.divInfo {
    text-align: center;
    width: 80%;
}
.descrip {
    text-align: justify;
}

/* Dispositivos extra pequeños (teléfonos, 600px y menos) */
@media only screen and (max-width: 600px) {
    form {
    width: 80%;
    }
    .cadaPelicula {
    width: 80%;
    }
    .divInfo {
    width: 100%;
    }
} 
/* Dispositivos pequeños (tablets verticales y teléfonos grandes, 600px y más) */ 
@media only screen and (min-width: 600px) and (max-width: 767px) {
    form {
    width: 55%;
    }
    .cadaPelicula {
    width: 80%;
    }
    .divInfo {
    width: 100%;
    }
}
</style>