<template>
  <Bienvenida
  v-on:iniciar="consumirAPI"
  v-on:seccionPeliculas="mostrarComponentePeliculas"
  v-on:ocultarSeccionPeliculas="ocultarComponentePeliculas"
  />
  <Peliculas v-if="mostrarPeliculas"
  v-bind:datosPeliculas="datosConsumidos"
  />
</template>

<script>
import Bienvenida from '@/components/Bienvenida.vue';
import Peliculas from '@/components/Peliculas.vue';

export default {
  name: 'App',
  components: {
    Bienvenida,
    Peliculas
  },
  data: function() {
    return {
      datosConsumidos:[],
      mostrarPeliculas: false
    }
  },
  methods: {
    consumirAPI: function() {
        let url = 'https://ghibliapi.vercel.app/Films';
        fetch(url)
        .then(response=> response.json())
        .then(data =>{
          let timeout= 1000;
          setTimeout(() => {
            this.datosConsumidos = data;
          }, timeout);
        })
        .catch(error=> console.log('Error al comsumir API', error));
    },
    mostrarComponentePeliculas: function() {
      this.mostrarPeliculas = true;
    },
    ocultarComponentePeliculas: function() {
      this.mostrarPeliculas = false;
    }
  }
}
</script>

